import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

const StyledWord = styled.span`
  display: inline-block;
  word-spacing: normal;
  letter-spacing: normal;
  line-height: 1;
  overflow: hidden;

  &:not(:last-child) {
    padding-right: 1rem;
  }

  & span {
    display: inline-block;
    transform: translateY(150%);
    opacity: 0;
    transition: transform 1.2s ease, opacity 0.9s 0s ease,
      -webkit-transform 0.6s ease;
    transition-delay: ${props =>
      props.transitionDelay ? props.transitionDelay : 0}ms;
  }

  &.page-loaded span {
    transform: translateY(0);
    opacity: 1;
  }
`

const Slicer = ({ children }) => {
  const [loaded, setLoaded] = useState(false)
  const words = children.split(' ')

  useEffect(() => {
    setLoaded(true)
  }, [])

  return (
    <>
      {words.map((word, idx) => {
        return (
          <StyledWord
            className={`word ${loaded ? 'page-loaded' : 'not-loaded'}`}
            transitionDelay={idx * 150}
            key={word}
          >
            <span>{word}</span>
          </StyledWord>
        )
      })}
    </>
  )
}

export default Slicer
